          <el-radio-group v-model="formShipping.type">
            <el-radio :label="0" :value="0">Không chọn</el-radio>
            <el-radio v-for="shipping of orderShipping" :label="shipping.id" :value="shipping.id">{{shipping.title}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="Tùy chỉnh:" v-if="!formShipping.delivery && !formShipping.type">
          <my-money
            placeholder="Giá"
            fixed=0
            v-model="formShipping.price"
