<script>
import { Message, Table, TableColumn, Input, Button, InputNumber, Dialog, Form, FormItem, Select, Option, Switch, Radio, RadioGroup, Col} from 'element-ui';
import MyMoney from 'src/components/UIComponents/Money'
import MyRegion from 'src/components/UIComponents/Region.vue'
import SelectAjax from 'src/components/UIComponents/SelectAjax'
import api from 'src/store/api';
import { mapState } from 'vuex'

export default {
  components: {
    ElTable: Table,
    ElTableColumn: TableColumn,
    ElInput: Input,
    ElButton: Button,
    ElDialog: Dialog,
    ElForm: Form,
    ElFormItem: FormItem,
    ElInputNumber: InputNumber,
    ElSelect: Select,
    ElOption: Option,
    ElSwitch: Switch,
    ElRadio: Radio,
    ElRadioGroup: RadioGroup,
    ElCol: Col,
    SelectAjax,
    MyMoney,
    Message,
    MyRegion
  },
  created() {
    let self = this;
    this.$validator.extend('required_region', {
      getMessage(field, val) {
        return 'Tỉnh/huyện không được rỗng'
      },
      validate(value, field) {
        if (!value.region && !value.subregion) return 0
        else return 1
      }
    })
  },
  data () {
    return {
      shippingFee: 0,
      coupon: '',
      couponDiscount: 0,
      notes: '',
      shippingPrice: 0,
      formCoupon: {
        type: 'VND',
        discount: 0,
        coupon: {
          handle: ''
        }
      },
      formShipping: {
        type: 0
      },
      formCustomer: {
        'customer': {
          dataRegion: {
            region: 0,
            subregion: 0,
          }
        },
        'shippingInfo': {
          dataRegion: {
            region: 0,
            subregion: 0,
          }
        }
      },
      listProduct: [],
      tableData: [],
      customer: [],
      dialogFormCouponVisible: false,
      dialogFormShippingVisible: false,
      editInfoCustomer: true,
      editShippingCustomer: true,
      searchProduct: false
    }
  },

  computed: {
    ...mapState({
      orderShipping: state => state.orderShipping,
    }),
    subTotal(){
      let total = 0;
      this.tableData.forEach(function(element, index){
        total += element['price']*element['quantity'];
      })
      return total;
    },
    discount() {
      if (this.formCoupon.type == '%') return (this.formCoupon.discount/100)*this.subTotal;
      else if (this.formCoupon.type == 'VND') return this.formCoupon.discount;
      else {
        return this.couponDiscount;
      }
    },
    total(){
      let result = this.subTotal + this.shippingFee - this.couponDiscount;
      return result<0?0:result;
    },
    host(){
      return window.BASE_URL;
    }
  },

  mounted() {
    this.$store.dispatch('setPageTitle', 'Tạo đơn hàng');
    this.$store.dispatch('setCurrentActions', [{
      label: 'Lưu lại',
      type: 'primary',
      icon: '',
      callback: this.save
    }])
  },

  methods: {
    deleteRow(index, value){
      let id = value['variantId'];
      this.tableData = this.tableData.filter((row) => {
        return row.variantId != id;
      });
    },
    closeRemote(){
      this.searchProduct = false;
    },
    cancel() {
      this.$router.push({name: 'AllOrders'});
    },
    save(status) {
      if (typeof status === 'object') status = 'new';
      let self = this;
      if (!this.tableData.length){
        Message({
          showClose: true,
          message: 'Phải chọn ít nhất một sản phẩm',
          type: 'error'
        });
        return;
      }
      this.$validator.validateAll().then(function (result) {
        if (result) {
          let data = {};
          data.customer = self.formCustomer.customer;
          data.customer.region = self.formCustomer.customer.dataRegion.region;
          data.customer.subregion = self.formCustomer.customer.dataRegion.subRegion;
          data.shipping_address = self.formCustomer.shippingInfo;
          data.shipping_address.region = self.formCustomer.shippingInfo.dataRegion.region;
          data.shipping_address.subregion = self.formCustomer.shippingInfo.dataRegion.subRegion;
          data.shipping_price = self.shippingFee;
          data.subTotal = self.subTotal;
          data.coupon = self.formCoupon.type == 'coupon'?self.formCoupon.coupon.handle:'';
          data.order_discount = self.discount;
          data.shipping = self.formShipping.type;
          data.notes = self.notes;
          data.status = status;
          data.cart = self.tableData;
          console.log('data', data);
          self.$store.dispatch('storeOrder', data).then(res => {
            Message({
              message: 'Tạo mới thành công',
              type: 'success'
            });
            if (status == 'new') self.$router.push({name: 'AllOrders'});
            else self.$router.push({name: 'DraftOrders'});
          }, res => {
            Message({
              message: res.message,
              type: 'error'
            });
          });
        }
      })
    },
    formatPrice(value) {
        let val = (value/1).toFixed(0).replace('.', ',')
        return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
    },
    remoteMethod(query) {
      this.searchProduct = true;
      api.get(`/admin/api/order/loadProduct?q=${query}`).then(resp => {
        let data = resp.data;
        this.listProduct = data.data.map(obj => obj);
      }).catch(e => {
        console.log('err', e);
      })
    },
    selectProduct(product, variant){
      let checkExist = this.tableData.find(function(obj) {
        if (obj.variantId === variant.id) {
          obj.quantity++;
          return true;
        }
        return false;
      });
      if (!checkExist) {
        this.tableData.push({
          productId: product.id,
          productTitle: product.title,
          variantTitle: variant.title,
          variantId: variant.id,
          price: variant.price,
          priceCompare: variant.price_compare,
          quantity: 1
        })
      }
      this.searchProduct = false;
    },
    handleCustomer(value){
      console.log('customer', this.customer);
      console.log('value', value);
    },
    editCustomer(){
      console.log('edit ino customer');
    },
    editShippingInfo(){
      console.log('edit info shipping')
    },
    addCoupon(){
      let self = this;
      if (this.formCoupon.type == 'coupon'){
        self.couponDiscount = 0;
        self.checkCoupon();
      } else if(self.formCoupon.type == '%') {
        let discount = self.total - (self.total / self.formCoupon.discount) * 100
        self.couponDiscount = discount || 0;
      } else self.couponDiscount = self.formCoupon.discount;
      this.dialogFormCouponVisible = false;
    },
    addShipping(){
      let self = this;
      if (this.formShipping.delivery) self.shippingFee = 0;
      else if (this.formShipping.type){
        let shipping = self.orderShipping.filter(item => {
          return item.id == self.formShipping.type
        })
        self.shippingFee = shipping[0].price||0;
      }
      else self.shippingFee = this.formShipping.price||0;
      this.dialogFormShippingVisible = false;
    },
    getShipping(newVal){
      let data = {};
      let self = this;
      data.region_id = newVal.region;
      data.subregion_id = newVal.subRegion;
      data.total = self.total;
      self.$store.dispatch('getShipping', data);
    },
    checkCoupon(){
      let self = this;
      if (self.formCoupon.type == 'coupon' && self.subTotal) {
        let data = {
          coupon: self.formCoupon.coupon.handle,
          subtotal: self.subTotal
        }
        self.$store.dispatch('checkCoupon', data).then(result => {
          self.couponDiscount = result.discount
        })
      }
    }
  },
  watch: {
    customer: {
      handler: function (newValue, oldValue) {
        api.get(`/admin/api/customer/${newValue.id}`).then(resp => {
          let customer = resp.data.customer;
          this.formCustomer.customer = this.formCustomer.shippingInfo = customer;
          this.formCustomer.customer.dataRegion.region = this.formCustomer.shippingInfo.dataRegion.region = customer.region;
          this.formCustomer.customer.dataRegion.subregion = this.formCustomer.shippingInfo.dataRegion.subregion = customer.subregion;
          this.editShippingCustomer = false;
          this.editInfoCustomer = false;
        }).catch(e => {
          console.log('err', e);
        })
      },
      deep: true
    },
    'formCustomer.shippingInfo.dataRegion'(newVal, oldVal) {
      this.getShipping(newVal);
    },
    total(newVal, oldVal){
      this.getShipping(newVal);
      this.checkCoupon();
    }
  },
  destroyed(){
    this.$store.dispatch('setCurrentActions', []);
    this.$store.state.orderShipping = [];
  }
}
</script>

